body {
  color: #47300b;
  background-color: #5b4f37;
  margin: 0;
  font-family: Tahoma, sans-serif;
  overflow-x: hidden;
}

h3 {
  margin: .5rem 0;
}

a {
  color: #f7e6ca;
  text-decoration: none;
}

a:hover {
  color: #fcf7ed;
}

.wrapper {
  width: 100%;
  height: 100%;
  flex-direction: column;
  padding: .5rem;
  display: flex;
}

.header {
  width: 25%;
  color: #47300b;
  background-color: #d6af85;
  border-radius: 10px 10px 0 0;
  justify-content: center;
  align-self: center;
  padding: .5rem 1rem;
  display: flex;
}

.footer {
  width: 25%;
  color: #47300b;
  background-color: #d6af85;
  border-radius: 0 0 10px 10px;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  padding: .5rem 1rem;
  display: flex;
}

.centered {
  align-self: center;
}

.main {
  width: 80%;
  background-color: #e6cdb3;
  border-radius: 5px;
  margin: auto;
  padding: 2rem 1rem;
  display: flex;
}

.cookieBox {
  text-align: center;
  flex-basis: 50%;
}

.upgradesBox {
  flex-basis: 50%;
}

.upgrade {
  padding: .5rem;
  display: flex;
}

.attrFrame {
  flex-direction: column;
  flex-basis: 50%;
  justify-content: end;
  display: flex;
}

.buttonFrame {
  text-align: center;
  flex-basis: 50%;
  justify-content: center;
}

#cookieButton {
  width: 400px;
  height: 400px;
  box-sizing: border-box;
  border-radius: 200px;
  transition: all 50ms ease-in-out;
}

.upBut {
  background-color: #b18a61;
  border: 3px solid #47300b;
  border-radius: 5px;
  place-self: center;
  padding: .5rem;
  transition: all 50ms ease-in-out;
}

.clickable {
  background-color: #d6af85;
}

.clickable:hover {
  background-color: #df9a2a;
}

